html {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

body {
  background: url("./Images/backgroundMain.jpg") no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*LogIn.js-SignUp.js LogIn and SignUp components */
.loginContainer, .signupContainer {
  background: url("./Images/backgroundLogin.jpg") no-repeat;
  background-position: center center;
  background-size: cover;
  color: #000000;
  font-size: 22px;
  height: 100vh;
  text-align: center;
  width: 100%;
}

.loginLogo {
  margin-top: 20px;
  width: 35%;
}

.loginForm, .signupForm {
  margin-top: 40px;
}

.emailInput, .passwordInput {
  background: rgba(255,255,255,0.4);
  border: 2px solid #FFFFFF;
  border-radius: 6px;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 19px;
  padding: 8px;
  width: 34%;
}

.loginButtons, .signupButton {
  background: #FFFFFF;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 19px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 15px;
  padding: 8px;
  width: 28%;
}

.loginButtons img {
  margin-right: 10px;
}

/*NotesTimeline.js NotesTimeline component */
.notesContainer {
  font-size: 17px;
  height: 100vh;
  text-align: center;
  width: 100%;
}

/*NavMenu.js NavMenu component */
.navContainer {
  display: grid;
  grid-template-columns: 33.32% 33.32% 33.32%;
  height: 22%;
  margin-bottom: 40px;
  align-items: center;
}

.navLogo {
  grid-column-start: 2;
  justify-self: center;
  width: 80%;
}

.logoutButton {
  font-size: 14px;
  font-weight: bold;
  grid-column-start: 3;
  justify-self: center;
  text-decoration: none;
}

.logoutButton img {
  margin-left: 6px;
  width: 25px;
}

/*NotesForm.js NotesForm component*/
.notesFormContainer {
  background-color: #EFC224;
  border-radius: 8px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 84% 14%;
  grid-template-rows: 30% 60%;
  height: 25%;
  margin: auto;
  margin-bottom: 30px;
  margin-top: 30px;
  padding: 10px;
  width: 40%;
}

.noteFormTitle {
  align-self: stretch;
  background: #E4BA23;
  border: none;
  grid-column-start: 1;
  grid-row-start: 1;
  justify-self: stretch;
  padding: 6px;
}

.noteFormBody {
  align-self: stretch;
  background: #E4BA23;
  border: none;
  grid-column-start: 1;
  grid-row-start: 2;
  justify-self: stretch;
  resize: none;
  padding: 6px;
}

.notePostIcon {
  cursor: pointer;
  grid-column-start: 2;
  grid-row-start: 1;
  justify-self: center;
}

.imagePostIcon {
  cursor: pointer;
  grid-column-start: 2;
  grid-row-start: 2;
  justify-self: center;
}

/*NotesList.js NotesList component*/
.noteListContainer {
  margin: auto;
  margin-top: 10px;
  height: auto;
  width: 90%;
}

.notePosted {
  background-color: rgba(242,100,68,0.9);
  border-radius: 8px;
  display: grid;
  float: left;
  grid-gap: 5px;
  grid-template-columns: 99%;
  grid-template-rows: 23% 50% 24%;
  height: 190px;
  margin: 10px;
  padding: 8px;
  width: 28%;
}

.titlePosted {
  align-self: stretch;
  font-weight: bold;
  grid-column-start: 1;
  grid-row-start: 1;
  justify-self: stretch;
  padding: 4px;
  word-wrap: break-word;
}

.bodyPosted {
  align-self: stretch;
  grid-column-start: 1;
  grid-row-start: 2;
  justify-self: stretch;
  padding: 4px;
  text-align: justify;
  word-wrap: break-word;
}

.notePostedButtons {
  align-self: center;
  cursor: pointer;
  grid-column-start: 1;
  grid-row-start: 3;
  justify-self: center;
  padding: 3px;
}

.deleteButton, .editButton {
  margin: 2px;
  width: 24px;
}

.deleteButton:hover, .editButton:hover {
  width: 26px;
}


/* Section for the media queries */
@media screen and (min-width: 700px) and (max-width: 940px) {
  .loginLogo {
    width: 55%;
  }

  .emailInput, .passwordInput {
    font-size: 20px;
    width: 58%;
  }
  
  .loginButtons, .signupButton {
    font-size: 20px;
    width: 45%;
  }

  .navLogo {
    width: 95%;
  }

  .logoutButton {
    justify-self: center;
  }

  .noteListContainer {
    width: 98%;
    margin-left: 15px;
  }

  .notePosted {
    margin: 7px;
  }

  .notesFormContainer {
    width: 60%;
  }
}

@media screen and (min-width: 441px) and (max-width: 699px) {
  .loginContainer, .signupContainer {
    background: url("./Images/backgroundLoginM.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
  }

  body {
    background: url("./Images/backgroundMainM.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
  }
  
  .loginLogo {
    width: 75%;
  }

  .emailInput, .passwordInput {
    width: 65%;
  }
  
  .loginButtons, .signupButton {
    width: 55%;
  }

  .navContainer {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 80% 20%;
    height: 28%;
    margin-bottom: 5px;
  }

  .navLogo {
    align-self: stretch;
    justify-self: center;
    grid-column-start: 1;
    grid-row-start: 1;
    width: 50%;
  }

  .logoutButton {
    align-self: center;
    font-size: 12px;
    grid-column-start: 1;
    grid-row-start: 2;
    justify-self: center;
  }
  
  .logoutButton img {
    align-self: center;
    width: 20px;
  }

  .noteListContainer {
    width: 100%;
    margin-left: 15px;
  }

  .notePosted {
    margin: 5px;
    width: 42%;
  }

  .notesFormContainer {
    width: 60%;
  }
}
@media screen and (min-width: 290px) and (max-width: 440px) {
  .loginContainer, .signupContainer {
    background: url("./Images/backgroundLoginM.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
    font-size: 18px;
  }

  body {
    background: url("./Images/backgroundMainM.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
  }
  
  .loginLogo {
    width: 80%;
  }

  .loginForm, .signupForm {
    margin-top: 10px;
    padding: 0 10px;
  }
  
  .emailInput, .passwordInput {
    font-size: 16px;
    margin-bottom: 10px;
    width: 80%;
  }
  
  .loginButtons, .signupButton {
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 2px;
    padding: 6px;
    width: 62%;
  }

  .navContainer {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 80% 20%;
    height: 28%;
    margin-bottom: 5px;
  }

  .navLogo {
    align-self: stretch;
    justify-self: center;
    grid-column-start: 1;
    grid-row-start: 1;
    width: 68%;
  }

  .logoutButton {
    align-self: center;
    font-size: 12px;
    grid-column-start: 1;
    grid-row-start: 2;
    justify-self: center;
  }
  
  .logoutButton img {
    align-self: center;
    width: 18px;
  }

  .noteListContainer {
    width: 100%;
    margin-left: 15px;
  }

  .notePosted {
    margin: 5px;
    width: 85%;
  }

  .notesFormContainer {
    width: 78%;
  }
}

@media screen and (min-height: 310px) and (max-height: 400px) {
  .loginContainer, .signupContainer {
    background: url("./Images/backgroundLoginM.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
    height: 730px;
  }
}

@media screen and (min-height: 401px) and (max-height: 600px) {
  .loginContainer, .signupContainer {
    background: url("./Images/backgroundLoginM.jpg") no-repeat;
    background-position: center center;
    background-size: cover;
    height: 690px;
  }
}
